
.wp-block-create-block-team-member-slider-block {
	display: block;
	@for $i from 1 through 6{
		&.has-#{$i}-columns{
			>.block-editor-inner-blocks >.block-editor-block-list__layout >{
				[data-type="create-block/team-member"]{
					width: calc(100%/ #{$i} - 20px);
				}
				.block-list-appender{
					align-self: stretch;
					width: calc(100%/ #{$i} - 20px);
					border: 1px dashed #000;
					display: flex;
					align-items: center;
					justify-content: center;
					min-height: 70px;
					position: relative;
				}
			}
		}
	}
	> .block-editor-inner-blocks >.block-editor-block-list__layout{
		display: flex;
		flex-wrap: wrap;

		[data-type="create-block/team-member"]{
			max-width: none;
			margin: 0;
		}
		> [data-type="create-block/team-member"]{
			.wp-block-create-block-team-member-img{
				position: relative;
				margin-bottom: 20px;

				&.is-loading img{
					opacity: 0.3;
				}
				.components-spinner{
					position: absolute;
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
					margin: auto;
				}
			}
			margin: 10px;
			h4{
				color: #222;
				font-size: 22px;
				font-weight: bold;
				margin: 0;
			}
			p{
				color:#565656;
				font-size: 16px;
				font-weight: 300;
				margin-bottom: 5px;
				margin-top: 5px
			}
		}
	}
}
